<template>
    <div class="v-calendar m-calendar">
        <main class="m-calendar-main" :class="getSloganMeta('style')" :style="topStyle">
            <!-- 年份切换 -->
            <section class="m-calendar-year">
                <el-button icon="el-icon-arrow-left" size="medium" :disabled="prevDisabled" @click="toggleYear('prev')" class="u-btn"></el-button>
                <span class="u-year">{{ current.year }}</span>
                <el-button icon="el-icon-arrow-right" size="medium" :disabled="nextDisabled" @click="toggleYear('next')" class="u-btn"></el-button>
            </section>
            <!-- 月份切换 -->
            <section class="m-calendar-month">
                <div class="m-calendar-slogan">
                    <a :href="getSloganMeta('url')" target="_blank"><img :src="getSloganMeta('banner')">{{ getSloganMeta('banner') }}</a>
                </div>
                <!-- 中央海报 -->
                <div class="u-slogan m-calendar-slogan">
                    <a :href="getSloganMeta('url')" target="_blank"><img :src="getSloganMeta('banner')" /></a>
                </div>
            </section>
            <section class="m-calendar-content">
                <section class="m-calendar-week">
                    <div class="u-week" v-for="week in weeks" :key="week">
                        <span>{{ week }}</span>
                    </div>
                </section>
                <section class="m-calendar-date">
                    <div
                        v-for="(item, index) in dataArr"
                        class="u-date"
                        @click.prevent="dateClick(item)"
                        :class="[
                            { 'u-other': ['pre', 'next'].includes(item.type) },
                            { 'u-today': isToday(item) },
                            { 'u-current': isCurrent(item) },
                        ]"
                        :key="index"
                    >
                        <calendar-item :data="item" :counts="counts" :slogans="slogans"></calendar-item>
                    </div>
                </section>
            </section>
        </main>
        <aside class="m-calendar-aside">
            <calendar-detail :date-obj="current"></calendar-detail>
        </aside>
    </div>
</template>

<script>
import { months, weeks } from "@/assets/data/calendar.json";
import { getCalendar, getCalendarCount, getCalendarSlogans } from "@/service/calendar.js";

import calendarDetail from "@/components/calendar/calendar_detail.vue";
import calendar_item from "@/components/calendar/calendar_item.vue";
export default {
    name: "Archive",
    components: {
        calendarDetail,
        "calendar-item": calendar_item,
    },
    data: () => ({
        current: {
            year: "",
            month: "",
            date: "",
        },
        months,
        weeks,

        dataArr: [],
        counts: [],
        slogans: [],
    }),
    computed: {
        // 禁止下一年
        nextDisabled() {
            const this_year = new Date().getFullYear();
            return this_year + 1 <= this.current.year;
        },
        // 禁止上一年
        prevDisabled() {
            // jx3 公测 2009
            return this.current.year <= 2009;
        },
        params() {
            const { year, month, date } = this.current;
            return {
                year,
                month,
                date,
            };
        },
        today() {
            return new Date().getDate();
        },
        client() {
            return this.$store.state.client;
        },
        pageSlogan() {
            const { current } = this;
            return this.slogans.find(
                (slogan) => slogan.year === current.year && slogan.month === current.month && !slogan.date
            );
        },
        topStyle() {
            return {
                backgroundColor: this.pageSlogan?.bgcolor,
                backgroundImage: `url(${this.pageSlogan?.img})`,
            };
        },
    },
    watch: {
        "$route.params": {
            immediate: true,
            handler: function ({ year, month, date }, oVal) {
                this.current = { year: ~~year, month: ~~month, date: ~~date || 1 };

                if (oVal?.year !== year || oVal?.month !== month || !oVal) {
                    this.getMonthData();
                    this.loadCalendar();
                    this.loadCalendarCount();
                }

                this.loadCalendarSlogans();
            },
        },
    },
    mounted() {
        // this.loadCalendar()
    },
    methods: {
        /**
         * 切换年份
         * @param {String}} action next 下一年 prev 上一年
         */
        toggleYear(action) {
            action === "prev" ? (this.current.year -= 1) : (this.current.year += 1);

            this.current.date = 1;

            this.$router.push(`/archive/${this.current.year}/${this.current.month}/${this.current.date}`);
        },
        /**
         * 切换月份
         * @param {number} num
         */
        toggleMonth(num) {
            this.current.month = num + 1;

            this.current.date = 1;

            this.$router.push(`/archive/${this.current.year}/${this.current.month}/${this.current.date}`);
        },
        // 获取指定月份数据
        getMonthData() {
            const { year, month } = this.current;
            let tmpEnd = 8;
            let dataArr = [];
            let daysInMonth = [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31];

            // 闰年
            if ((year % 4 === 0 && year % 100 !== 0) || year % 400 === 0) {
                daysInMonth[1] = 29;

                [3].includes(month) && (tmpEnd = 7);
            }

            const monthStartWeekday = new Date(year, month - 1, 1).getDay();
            const monthEndWeekday = new Date(year, month, 1).getDay() || 7;

            const preInfo = this.getPreMonth(this.current);
            const nextInfo = this.getNextMonth();

            for (let i = 0; i < monthStartWeekday - 1; i++) {
                let preObj = {
                    type: "pre",
                    date: daysInMonth[preInfo.month - 1] - (monthStartWeekday - i - 2),
                    month: preInfo.month,
                    year: preInfo.year,
                    children: [],
                };
                dataArr.push(preObj);
            }

            for (let i = 0; i < daysInMonth[month - 1]; i++) {
                let itemObj = {
                    type: "normal",
                    date: i + 1,
                    month,
                    year,
                    children: [],
                };
                dataArr.push(itemObj);
            }

            for (let i = 0; i < tmpEnd - monthEndWeekday; i++) {
                let nextObj = {
                    type: "next",
                    date: i + 1,
                    month: nextInfo.month,
                    year: nextInfo.year,
                    children: [],
                };
                dataArr.push(nextObj);
            }

            this.dataArr = dataArr;
        },
        // 获取前一个月的年月日信息
        getPreMonth(date, defaultDate = 1) {
            let { year, month } = date || this.current;

            if (month === 1) {
                year -= 1;
                month = 12;
            } else {
                month -= 1;
            }

            return { year, month, date: defaultDate };
        },
        // 获取后一个月的年月日信息
        getNextMonth(defaultDate = 1) {
            let { year, month } = this.current;
            if (month === 12) {
                year += 1;
                month = 1;
            } else {
                month += 1;
            }

            return { year, month, date: defaultDate };
        },
        dateClick({ date, month, year }) {
            this.current.year = year;
            this.current.month = month;
            this.current.date = date;

            this.$router.push(`/archive/${this.current.year}/${this.current.month}/${this.current.date}`);
            // this.dataArr = this.getMonthData();
        },
        isToday({ year, month, date }) {
            const dateObj = new Date();

            return dateObj.getFullYear() === year && dateObj.getMonth() + 1 === month && dateObj.getDate() === date;
        },
        isCurrent({ year, month, date }) {
            const { current } = this;

            return current.year === year && current.month === month && current.date === date;
        },

        // 数据加载
        loadCalendar() {
            const { year, month } = this.current;
            getCalendar({ year, month }, this.client).then((res) => {
                const data = res.data.data;
                data?.forEach((item) => {
                    let { year, month, date } = item;
                    let index = this.dataArr.findIndex((d) => d.year === year && d.month === month && d.date === date);

                    if (index) {
                        this.dataArr[index].children.push(item);
                    }
                });
            });
        },
        loadCalendarCount() {
            const { year, month } = this.current;
            getCalendarCount({ year, month }).then((res) => {
                this.counts = res.data.map((item) => {
                    return {
                        ...item,
                        month,
                        year,
                    };
                });
            });
        },
        loadCalendarSlogans() {
            const { year, month } = this.current;
            getCalendarSlogans({ year, month }).then((res) => {
                this.slogans = res.data;
            });
        },

        // 过滤
        getSloganMeta(key) {
            return this.pageSlogan?.[key];
        },
    },
};
</script>
